.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.title{
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 72px;
  color: #000000;
}

.card-wrapper{
  display: flex;
}

.container{
  color:  #0A0A0C;
  
}

.card{
  border: 0.5px solid #CDCDCD;
  border-radius: 8px;
  width: 80px;
  height: 104px;
  margin: 7.5px;
  cursor: pointer;
  display: inline-block;
  justify-content: center;
  align-items: center;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
}

.card-value{
  position: relative;
  top: 30%;
  pointer-events: none;
  user-select: none;
}

.clicked{
  color: #0A0A0C;
  border: 0.5px solid #ED5517;
  border-radius: 8px;
}

.hide{
  color: #ffffff;
}

.disable{
  pointer-events: none
}

.show{
  color: #0A0A0C;
  border: 0.5px solid #CDCDCD;
}

.game-end_wrap{
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  display: none;
}

.game-end{
  position: absolute;
  color: #b5101d;
  font-size: 46px;
  font-weight: 600;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -30%);
  
}

.finish{
  display: block;
}